<template>
	<div id="staffContainer">
		<div class="header">
			<el-radio-group v-model="linkto" fill="#4877E7" @change="changer">
				<el-radio-button label="员工管理"></el-radio-button>
				<el-radio-button label="职级管理"></el-radio-button>
			</el-radio-group>
			<router-link :to="{path:'/staff/invitStaff'}">
			<el-button class="right invite" v-if="showTreeTable==true">邀请员工</el-button>
			</router-link>
			<router-link :to="{path:'/staff/editDuty'}">
			<el-button class="right invite" v-if="showTreeTable==false">添加职位</el-button>
			</router-link>
		</div>
		<position-management v-if="showTreeTable==false"></position-management>
		<div v-else>
			<staff-tree class="staff-tree"></staff-tree>
			<staff-table class="staff-table"></staff-table>
		</div>
	</div>
</template>

<script>
	import StaffTree from './staffTree';
	import StaffTable from './staffTable';
	import positionManagement from "./positionManagement"
	export default {
		data() {
			return {
				linkto: "员工管理",
				showTreeTable: true
			}
		},
		components: {
			StaffTree,
			StaffTable,
			positionManagement
		},
		created() {
			if(this.$route.query.name !== undefined) {
				this.linkto = "职级管理"
				this.showTreeTable =false
			}
			},
		methods: {
			changer() {
				if(this.linkto == "职级管理") {
					this.showTreeTable = false;
				} else {
					this.showTreeTable = true;
				}
			},
			invitStaff() {
				
			}

		}
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	#staffContainer {
		position: relative;
		@include wh(100%, 100%);
	}
	
	.header {
		width: 100%;
		height: 50px;
		padding: 0 14px;
	}
	
	.invite {
		@include wh(80px, 34px);
		border: 1px solid #4877e7;
		color: #4877e7;
		border-radius: 0;
		font-size: 14px;
	}
	
	.staff-tree {
		width: 316px;
		position: absolute;
		top: 50px;
		bottom: 0;
		left: 0;
		background-color: #fff;
	}
	
	.staff-table {
		margin-left: 330px; // height: 1052px;
		background-color: #fff;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#staffContainer{
	.el-radio-group .el-radio-button {
		margin-right: 15px;
		color: #4877E7;
	}
	
	.el-radio-button .el-radio-button__inner {
		width: 80px;
		height: 33px;
		padding: 9px;
		border: none;
		border: 1px solid #4877E7;
	}
	
	.el-radio-button:first-child .el-radio-button__inner,
	.el-radio-button:last-child .el-radio-button__inner {
		border-radius: 4px;
	}
	}
</style>


